import Vue from 'vue'
import {Navbar, Layout, Media, Image} from 'bootstrap-vue/es/components'

import BContainer from 'bootstrap-vue/es/components/layout/container'
import BMedia from 'bootstrap-vue/es/components/media/media'
import BImg from 'bootstrap-vue/es/components/image/img'
import BRow from 'bootstrap-vue/es/components/layout/row'
import BCol from 'bootstrap-vue/es/components/layout/col'
import {BForm, FormSelectPlugin} from 'bootstrap-vue/es/components'


import 'bootstrap/dist/css/bootstrap.css'


Vue.use(Navbar, Layout, Media, Image)
Vue.component('b-form', BForm)
Vue.component('b-form-select', FormSelectPlugin)
Vue.component('b-container', BContainer)
Vue.component('b-media', BMedia)
Vue.component('b-img', BImg)
Vue.component('b-row', BRow)
Vue.component('b-col', BCol)